body {
  margin: 0;
  padding: 0;
  width: 100%;
  background-color: #F1F8E9; /* Leafy Light Green */
  color: #2E3B32; /* Dark Green */
  font-family: "Poppins", sans-serif;
}

h1, h2, h3, h4 {
  font-weight: 600;
  color: #2E7D32; /* Primary Green */
}

button {
  background-color: #2E7D32;
  color: white;
  padding: 10px 16px;
  border-radius: 8px;
  transition: all 0.3s ease-in-out;
}

button:hover {
  background-color: #1B5E20; /* Darker Green on hover */
  transform: scale(1.05);
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 400px;
  position: relative;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 20px;
  cursor: pointer;
}

